@import "../../styles/colors.scss";
@import "../../styles/mixins.scss";

.cart {
  width: 1920px;
  .container {
    @include container;
    z-index: 0;
    .cart_products {
      .empty_cart {
        width: 100%;
        height: 800px;
        position: relative;
        h2 {
          position: absolute;
          top: 30%;
          left: 33%;
          color: $base-grey;
          font-size: 48px;
          font-weight: 500;
          text-transform: uppercase;
          opacity: 0.6;
          text-align: center;
          vertical-align: middle;
          margin: 0 auto;
        }
      }
    }
  }
}
